<template>
  <v-container class="d-flex flex-column px-0">
    <v-app-bar fixed class="top" app color="white">
      <v-btn fab text small @click="$router.back(-1)" class="mt-2">
        <v-icon large>mdi-arrow-left</v-icon>
      </v-btn>
      <span class="text_middle mt-2">回复我的</span>
    </v-app-bar>
    <div class="main_container_replyMe" style="background-color: #f5f5f5;">
      <v-tabs grow v-model="tabs">
        <v-tab>回复我的评论</v-tab>
        <v-tab>回复我的帖子</v-tab>

        <v-tab-item
          style="background-color: #f5f5f5;"
          class="mescroll_container_replyMe"
          v-touch:swiperight="rightSlide"
        >
          <mescroll-vue ref="mescroll" :down="mescrollDown1" :up="mescrollUp1">
            <div id="noMoreDataWrap1"></div>
            <v-card
              color="white"
              width="100%"
              class="mt-2"
              flat
              rounded
              v-for="(item,i) in ReplyMyComments"
              :key="i"
              @click="$router.push({
          path:'/postDetail',
          query:{id:item.id}
        });"
            >
              <v-card-text class="d-flex flex-column py-2">
                <div class="d-flex">
                  <v-avatar size="40" class="ml-3 mb-1">
                    <img :src="item.avatar" alt="头像" />
                  </v-avatar>
                  <div class="d-flex flex-column">
                    <span class="text_small-- text_thick ml-2">#{{item.name}}</span>
                    <span
                      class="text_small--- ml-2 grey--text mt-n1"
                    >{{timeFormat(item.created_time)}}</span>
                  </div>
                </div>
                <div>
                  <span class="text_thick black--text">【回复我】:</span>
                  <span
                    v-if="item.commentDeleteId"
                    class="black--text text-decoration-line-through"
                  >已被删除</span>
                  <div v-else>
                    <span
                      v-if="item.filterLevel==1"
                      class="black--text text-decoration-line-through"
                    >已折叠</span>
                    <span
                      v-if="item.filterLevel==2"
                      class="black--text text-decoration-line-through"
                    >已屏蔽</span>
                    <span v-if="item.filterLevel==0" class="black--text">{{item.content}}</span>
                  </div>
                </div>
                <div class="grey_background_replyMe py-2 px-2 mb-1 d-flex flex-column">
                  <span class="ml-2 text_small---">【我】：{{item.my_content}}</span>
                  <v-btn
                    v-if="item.my_image!=undefined&&item.my_image!='undefined'"
                    color="grey"
                    outlined
                    small
                    width="84px"
                    height="24px"
                    class="ml-2"
                    disabled
                  >
                    <v-icon left small>mdi-image</v-icon>
                    <span class="ml-n1">[图片]</span>
                  </v-btn>
                </div>
                <span class="text_small-- black--text">
                  <span class="text_thick">【帖子】</span>
                  {{item.title}}
                </span>
              </v-card-text>
            </v-card>
          </mescroll-vue>
        </v-tab-item>

        <v-tab-item style="background-color: #f5f5f5;" class="mescroll_container_replyMe">
          <mescroll-vue ref="mescroll" :down="mescrollDown2" :up="mescrollUp2">
            <div id="noMoreDataWrap2"></div>
            <v-card
              color="white"
              width="100%"
              class="mt-2"
              flat
              rounded
              v-for="(item,i) in ReplyMyPosts"
              :key="i"
              @click="$router.push({
          path:'/postDetail',
          query:{id:item.id}
        });"
            >
              <v-card-text class="d-flex flex-column py-2">
                <div class="d-flex">
                  <v-avatar size="40" class="ml-3 mb-1">
                    <img :src="item.avatar" alt="头像" />
                  </v-avatar>
                  <div class="d-flex flex-column">
                    <span class="text_small-- text_thick ml-2">#{{item.name}}</span>
                    <span
                      class="text_small--- ml-2 grey--text mt-n1"
                    >{{timeFormat(item.created_time)}}</span>
                  </div>
                </div>
                <div>
                  <span class="text_thick black--text">【回复我】:</span>
                  <span
                    v-if="item.commentDeleteId"
                    class="black--text text-decoration-line-through"
                  >已被删除</span>
                  <div v-else>
                    <span
                      v-if="item.filterLevel==1"
                      class="black--text text-decoration-line-through"
                    >已折叠</span>
                    <span
                      v-if="item.filterLevel==2"
                      class="black--text text-decoration-line-through"
                    >已屏蔽</span>
                    <span v-if="item.filterLevel==0" class="black--text">{{item.content}}</span>
                  </div>
                </div>
                <div class="grey_background_replyMe py-2 px-2 mb-1 d-flex flex-column">
                  <!-- <span class="text_small- text_thick">#JD57BY</span> -->
                  <span class="ml-2 text_small---">【我的帖子】：{{item.title}}</span>
                </div>
              </v-card-text>
            </v-card>
            <div class="d-flex justify-center mt-2" style="background-color:white">
              <v-progress-circular
                class="mt-1"
                v-if="!if_list_bottom2&&!atTop"
                indeterminate
                color="primary"
              ></v-progress-circular>
              <span v-if="if_list_bottom2">已经到底啦</span>
            </div>
          </mescroll-vue>
        </v-tab-item>
      </v-tabs>
    </div>
  </v-container>
</template>

<script>
import MescrollVue from "mescroll.js/mescroll.vue";
import touch from '../../utils/touch'
var shieldComment = (data, shieldCommentUserId) => {
  try {
    if (localStorage.getItem('shieldCommentUserId'))
      var shieldCommentUserId = JSON.parse(localStorage.getItem('shieldCommentUserId'))
  } catch (e) {
    var shieldCommentUserId = undefined
  }
  if (!shieldCommentUserId) return data;
  data.forEach(item => {
    if (shieldCommentUserId.includes(item.commentUserId))
      item.filterLevel = 2
  })
  return data;
}
export default {
  name: "replyMe",
  components: {
    MescrollVue, // 注册mescroll组件
  },
  data() {
    return {
      tabs: 0,
      ReplyMyComments: [],
      ReplyMyPosts: [],
      if_list_bottom1: false, //"回复我的评论"是否加载完成
      if_list_bottom2: false, //"回复我的帖子"是否加载完成
      atTop: true, //是否处于顶端

      mescrollDown1: {
        use: false,
      },
      mescrollUp1: {
        callback: this.getMoreData1,
        empty: {
          warpId: 'noMoreDataWrap1',
          icon: null,
          tip: "暂无回复我的评论",
          btntext: "",
          btnClick: null,
          supportTap: false
        }
      },
      mescrollDown2: {
        use: false,
      },
      mescrollUp2: {
        callback: this.getMoreData2,
        empty: {
          warpId: 'noMoreDataWrap2',
          icon: null,
          tip: "暂无回复我的帖子",
          btntext: "",
          btnClick: null,
          supportTap: false
        }

      },
    };
  },
  mounted() {
  },
  created() {
    let that = this;
    if (sessionStorage.getItem('commentNumTemp'))
      localStorage.setItem('commentNum', sessionStorage.getItem('commentNumTemp'))

  },

  methods: {
    //计算时间差
    timeFormat(latest_comment_time) {
      //一共这么多秒前
      let t = Math.floor(
        (new Date().getTime() - new Date(latest_comment_time).getTime()) / 1000
      );
      const d = Math.floor(t / 3600 / 24);
      const h = Math.floor(t / 3600) % 24;
      const m = Math.floor(t / 60) % 60;
      const s = Math.floor(t % 60);
      let res = "";
      if (s > 0 && m == 0 && h == 0 && d == 0) {
        res = s + "秒前";
      } else if (m > 0 && h == 0 && d == 0) {
        res = m + "分钟前";
      } else if (h > 0 && d == 0) {
        res = h + "小时前";
      } else {
        let date = new Date(
          new Date(latest_comment_time).getTime() + 8 * 3600 * 1000
        );
        return date.toJSON().substr(0, 16).replace("T", " ");
      }
      return res;
    },
    getMoreData1(page, mescroll) {
      console.log('1', page)
      let that = this;
      let latest_comment_time =
        that.ReplyMyComments.length > 0
          ? that.ReplyMyComments[that.ReplyMyComments.length - 1].created_time
          : new Date();
      that.$request('get', '/user/getReplyMyComment', {
        timeToken: new Date(latest_comment_time).getTime()
      })
        .then((res) => {
          shieldComment(res.data)
          that.ReplyMyComments.push(...res.data);

          mescroll.endSuccess(res.data.length);
        })
        .catch((e) => {
          console.log(e);
          mescroll.endErr();
        });
    },
    getMoreData2(page, mescroll) {
      console.log('2', page)
      let that = this;
      let latest_comment_time =
        that.ReplyMyPosts.length > 0
          ? that.ReplyMyPosts[that.ReplyMyPosts.length - 1].created_time
          : new Date();
      that.$request('get', '/user/getReplyMyPost', {
        timeToken: new Date(latest_comment_time).getTime()
      })
        .then((res) => {
          shieldComment(res.data)
          that.ReplyMyPosts.push(...res.data);
          mescroll.endSuccess(res.data.length);
        })
        .catch((e) => {
          console.log(e);
          mescroll.endErr();
        });
    },
    rightSlide() {
      if (this.tabs === 0)
        this.$router.go(-1)
    }
  },
  directives: {
    touch
  },

};
</script>

<style>
.main_container_replyMe {
  margin-top: 50px;
}
.grey_background_replyMe {
  background-color: #f5f5f5;
  border-radius: 10px;
  width: 90%;
}

.mescroll_container_replyMe {
  position: fixed;
  top: 110px;
  bottom: 0;
  height: auto; /*如设置bottom:50px,则需height:auto才能生效*/
  width: 100%;
  background-color: #f5f5f5;
}
</style>



